
/* //////////////...................HEADER CSS ENDS.......................///////////////// */

.banner {
  width: 100%;
  height: 91vh;
  background-color: #3b3b3b;
  padding: 50px 5%;
  margin-top: 9vh;
  display: inline-block;
}

.top {
  width: 100%;
  height: 100%;
  background-color: blue;
  overflow: hidden;
  border-radius: 10px;
  position: relative;
}

.top > img {
  width: 100%;
  position: absolute;
}

.top-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  display: flex;
  align-items: flex-end;
}
.top-details {
  width: 40%;
  height: 50%;
  /* background-color: white; */
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  align-items: flex-start;
}

.top-details > h1 {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 40px;
}
.top-details > p {
  font-size: 18px;
}

.top-details > button {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  background-color: rgb(175, 64, 64);
  text-transform: uppercase;
  font-weight: bold;
}

/* //////////////...................BANNER CSS ENDS.......................///////////////// */

.category {
  width: 100%;
  padding: 30px 5%;
}
.category:nth-child(odd){
    background-color: #595959;

}
.category:nth-child(even){
    background-color: #3b3b3b;

}
.category-title {
  color: rgb(175, 64, 64);
  font-size: 30px;
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.movies-list {
  width: 100%;
  padding: 10px 0px;
  margin-top: 30px;
  display: flex;
  column-gap: 2.5%;
  flex-wrap: wrap;
  row-gap: 20px;
  /* background-color: red; */
}

.movie {
  height: 300px;
  width: 18%;
  background-color: rgb(175, 64, 64);
  overflow: hidden;
  border-radius: 10px;
  position: relative;
  display: flex;
}

.movie > img {
  width: 100%;
  position: absolute;
}

.movie-overlay{
    width: 100%;
    height: 45%;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    align-self: flex-end;
    padding: 10px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.movie-title{
    font-size: 23px;
    font-weight: bold;
}
.utils{
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}
button{
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    background-color: rgb(175, 64, 64);
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}
.player-button{
  /* width: 40%; */
  display: flex;
  column-gap: 20px;
}
.rating{
    display: flex;
    flex-direction: row;
    column-gap: 5px;
    align-items: center;
    justify-content: center;
}