.cred-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("https://wallpaperaccess.com/full/1900851.png");
  background-position: center 58%;
  /* background-color: red; */
  /* position: relative;
  top: 90px; */
}
.form-container {
  width: 25%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* align-items: center; */
  row-gap: 10px;
  box-shadow: 0px 0px 10px 10px rgb(0 0 0 / 50%);
}
.form-container > a > button{
  display: block;
  width: 100%;
  
}
.header {
  width: 100%;
  height: 9vh;
  padding: 20px 5%;
  display: flex;
  background-color: rgb(49, 45, 45);
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 10;
}
.logo {
  height: 40px;
  width: 12%;
  color: rgb(175, 64, 64);
  /* font-size: 20px; */
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.logo > a {
  text-decoration: none;
  color: rgb(175, 64, 64);
  font-size: 30px;
}
.logo > h1 {
  font-size: 30px;
}
.nav-two {
  width: 30%;
  height: 40px;
  /* background-color: gray; */
  display: flex;
  justify-content: space-between;
  column-gap: 5px;
}
.search {
  width: 90%;
  display: flex;
  background-color: #535353;
  border-radius: 10px;
  overflow: hidden;
}

.search > input {
  width: 80%;
  text-indent: 20px;
  background-color: #535353;
  border: none;
  outline: none;
  color: white;
}
.search > p {
  width: 20%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
}

.profile {
  width: 10%;
  height: 40px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  position: relative;
}
.profile i {
  cursor: pointer;
}

.search-result {
  width: 100%;
  height: 91%;
  background-color: #595959;
  position: fixed;
  top: 9vh;
  z-index: 10;
}

.menu {
  width: 10%;
  padding: 10px 20px;
  background-color: #8f8686;
  position: fixed;
  z-index: 10;
  top: 9vh;
  right: 2%;
  border-radius: 15px;
}

.menu ul {
  padding: 0px;
  margin: 0px;
}

.menu ul li {
  list-style-type: none;
  padding: 10px 0px;
  margin: 0px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

.menu ul li:hover {
  font-weight: bold;
  color: rgb(175, 64, 64);
}

/*.................... footer css......................... */
.footer-container{
 background-color: #302d2b;
}
.container>footer{
  background-color: #302d2b;
  border-radius: 20px;
}

.copyright{
  display: block;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}



.design{
  width: 16%;
  background-color: red;
  padding: 10px 10px;
  position: fixed;
  bottom: 10px;
  right: 5px;
  border-radius: 20px;
  background-color: rgb(175, 64, 64);
  color: white;
  font-weight: bolder;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
}